import React from "react";
import TypewriterComponent from "typewriter-effect";
import { TypeWriterStyled } from "./TypeWriter.styles";

const TypeWriter = () => {
  return (
    <TypeWriterStyled>
      <TypewriterComponent
        onInit={(typewriter) => {
          typewriter
            // .pauseFor(300)
            .typeString(
              "<h1>Лучше всего о моем качество и надежност <br/>расскажет мой портфолио</h1>"
            )
            .deleteAll()
            .start();
        }}
      ></TypewriterComponent>
    </TypeWriterStyled>
  );
};

export default TypeWriter;
