import styled from "styled-components";

export const TypeWriterStyled = styled.div`
  width: 100vw;
  height: 100vh;
  background: ${({ theme }) => theme.body};
  color: ${({ theme }) => theme.secondary};
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: clamp(20px, 1vw, 40px);
`;
